import React, { useEffect, useState } from 'react';
import './styles/Home.css';
import './styles/Mobile.css';
import { Link } from 'react-router-dom';

function Settings() {
    const [feelings, setFeelings] = useState([]);
    const [editingIndex, setEditingIndex] = useState(null);
    const [modifiedFeelings, setModifiedFeelings] = useState({});
    const [selectedOption, setSelectedOption] = useState('');
    const [pseudo, setPseudo] = useState("");
    const [notification, setNotification] = useState('');

    const handleSelectionChange = (e) => {
        console.log("1")
        const newTheme = e.target.value;
        setSelectedOption(newTheme);
        document.body.className = newTheme;
        
        if (pseudo) {
            fetch('/setTheme', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ pseudo, theme: newTheme }),
            })
            .then(res => res.json())
            .then(data => {
                console.log("set theme data",data)
                if (!data.success) {
                    console.error("chec de l'enregistrement du thme.");
                }
            })
            .catch(err => console.error("Erreur en envoyant le thme :", err));
        }
    };
    

    useEffect(() => {
        
        fetch('/getFeelings')
        .then(res => res.json())
        .then(data => {

            setFeelings(data.feelings)
            const donnees = data
            const pseudo2 = data.pseudo; 
            setPseudo(pseudo2);
        })
        .catch(err => console.error("Erreur en rcuprant les motions :", err));

    if (pseudo) {
        fetch('/getTheme', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ pseudo })
        })
        .then(res => res.json())
        .then(data => {
            if (data.success && data.theme) {
                setSelectedOption(data.theme);
                document.body.className = data.theme;
            }
        })
     
        .catch(err => console.error("Erreur en rcuprant le thme :", err));
    }
    }, []);

    useEffect(() => {
        if (pseudo) {
            fetch('/getTheme', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ pseudo })
            })
            .then(res => res.json())
            .then(data => {
                if (data.success && data.theme) {
                    setSelectedOption(data.theme);
                    document.body.className = data.theme;
                }
            })
            .catch(err => console.error("Erreur en rcuprant le thme :", err));
        }
    }, [pseudo]); // Se dclenche uniquement quand pseudo est dfini
    

    const handleInputChange = (index, value) => {
        setModifiedFeelings(prev => ({
            ...prev,
            [index]: value
        }));
    };

    const updateFeeling = (index) => {
        const newFeeling = modifiedFeelings[index];
        if (!newFeeling) return;

        const updatedFeelings = [...feelings];
        updatedFeelings[index] = newFeeling;
        setFeelings(updatedFeelings);
    
        fetch('/updateFeeling', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ index, newFeeling })
        })
        .then(res => res.json())
        .then(data => {
            if (!data.success) {
                setNotification("motion modifie !");
                setTimeout(() => setNotification(''), 3000);

            } else {
                setEditingIndex(null);
                setModifiedFeelings(prev => {
                    const updated = { ...prev };
                    delete updated[index];
                    return updated;
                });
            }
        })
        .catch(err => {
            console.error("Erreur en mettant  jour :", err);
        });
    };
    
    
    return (
        <div className="container">
            <div className="header">
                <h1>
                    <span>m</span><span>y</span><span>D</span><span>a</span><span>y</span>
                </h1>
            </div>
            
            <h5>Theme : </h5>

            <select id="theme-select" className="theme-select" value={selectedOption} onChange={handleSelectionChange}>
                <option value="colorful">Printemps</option>
                <option value="ocean">Ocan</option>
                <option value="dark">Sombre</option>
            </select>

            <h5>Emotions :</h5>
            <ol>
                {feelings.map((f, index) => (
                    <ol key={index}>
                        {editingIndex === index ? (
                            <>
                                <input
                                    className='inputFeelingsSettings'
                                    type="text"
                                    value={modifiedFeelings[index] ?? f}
                                    onChange={(e) => handleInputChange(index, e.target.value)}
                                />
                                <button 
                                    className="button-option"
                                    onClick={() => {
                                        updateFeeling(index)
                                        setModifiedFeelings(prev => {
                                            const updated = { ...prev };
                                            delete updated[index];
                                            setTimeout(() => {
                                                setEditingIndex(null);
                                            }, 3000);
                                            
                                            return updated;
                                            
                                        });
                                        }
                                    }    
                                >
                                </button>
                               
                                <button 
                                    className="button-option"
                                    onClick={() => {
                                        setEditingIndex(null);
                                        setModifiedFeelings(prev => {
                                            const updated = { ...prev };
                                            delete updated[index];
                                            return updated;
                                        });
                                    }}
                                ></button>
                                 {notification && <div className="popup-success-emotions">{notification}</div>}
                            </>
                        ) : (
                            <>
                                {f}
                                <button 
                                    className="button-option"
                                    onClick={() => setEditingIndex(index)}
                                ></button>
                            </>
                        )}
                        {index < feelings.length - 1 && <hr className="hr-settings" />}
                    </ol>
                ))}

            </ol>

            <Link to="/EmotionsSettings">
                <button type="button" className="submit-button">Roue des motions</button>
            </Link>

            <hr className="hr" />

            <Link to="/">
                <button type="button" className="submit-button">Mes motions</button>
            </Link>
            <hr className="hr" />

            <Link to="/DelUser">
                <button type="button" className="submit-button-supprimer">Supprimer compte</button>
            </Link>

            <p className="droits">
                 2025 myDay. Tous droits rservs. Cette application, ainsi que lensemble de son contenu, est protge par les lois en vigueur relatives  la proprit intellectuelle. Les donnes quelle contient sont chiffres afin den garantir la scurit.
            </p>
        </div>
    );
}

export default Settings;
